import React, { PureComponent } from 'react'
import { ActivityIndicator, StyleSheet, View, Text } from 'react-native'
import { Logger } from '../../utils/Utils';

export default class Message extends PureComponent {
  constructor(props) {
    super(props);

    this.state = props.messageState;
    Logger.debug('Message messageState', this.state);
  }

  render() {
    if (this.state && !this.state.playbackStatus ||
      this.state && typeof this.state.playbackStatus.playableDurationMillis === 'undefined' ||
      this.state && this.state.playbackStatus.playableDurationMillis - this.state.playbackStatus.positionMillis < 1000 || this.state && this.state.errorMessage) {
      return (
        <View style={[styles.container, styles.horizontal]}>
          <Text style={styles.textNotify}>{this.state.playbackStatus?this.state.playbackStatus.error:this.state.errorMessage}</Text>
        </View>
      )
    }
    return null;
  }
}

const styles = StyleSheet.create({
  container: {
    backgroundColor: 'rgba(0, 0, 0, 0.5)',
    flex: 1,
    justifyContent: 'center',
    width: '100%',
    height: '100%',
    alignItems: 'center'
  },
  horizontal: {
    flexDirection: 'row',
    justifyContent: 'space-around',
    padding: 10
  },
  textNotify:{
    color: 'white',
    alignSelf: 'center',
  }
})
